import React, { Component, useEffect, useState } from 'react';
// import { Helmet } from "react-helmet";
import DocumentMeta from 'react-document-meta';

import './design-help.css';

import PageBanner from '../../components/ui/page-banner/page-banner';
import SectionHeading from '../../components/pages-component/section-heading/section-heading';
import DesignHelpForm from './components/design-help-form/design-help-form';
import Modal from '../../components/ui/modal/modal';
import FormSuccessMsg from '../../components/pages-component/form-success-msg/form-success-msg';
import DigitalHelp from '../../services/api/post-DigitalHelp-query';
import getQueryReport from '../../services/api/get-queries-report'
import * as utilityFunctions from '../../utility-functions/utility-functions';
import { connect } from 'react-redux';
import Header from '../../components/layout/header/header';
import Footer from '../../components/layout/footer/footer';

const DesignHelp = (props) => {

  const [ formData, setformData ] = useState(new FormData())
  const [ fisStateRequired, setisStateRequired ] = useState(false)
  const [ allowStateField, setallowStateField ] = useState(false)

  const [ formValid, setformValid ] = useState(true);
  const [ errorMsg, seterrorMsg ] = useState(null);
  const [ formSubmissionStart, setformSubmissionStart ] = useState(false);
  const [ showModal, setshowModal ] = useState(false);

  useEffect(() => {
    window.scrollTo(0, 0);
  },[])

  const onClearningFormData = () => {
    props.formData.delete('name');
    props.formData.delete('country');
    props.formData.delete('contact_number');
    props.formData.delete('email');
    props.formData.delete('imagelink');
    props.formData.delete('message');
  }

  const resetProperties = () => {
    props.natureOfQuery = null;
    props.isStateRequired = false;
    props.allowStateField = false;
  }

  const onSubmitCallBack = (data) => {
    if (data.status === 201) {
      document.querySelector(".form--contact-us").reset();
      onClearningFormData();
      resetProperties();
      utilityFunctions.clearSelectField('.select-input');
      setformValid(true)
      seterrorMsg(null)
      setformSubmissionStart(false)
      setshowModal(true)
    } else {
      if (data.response) {
        // console.log(data.response.data);
        setformValid(false)
        seterrorMsg(data.response.data)
        setformSubmissionStart(false)
        // console.log(natureOfQuery);
      } else {
        const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
        setformValid(false)
        seterrorMsg(genericErrorMsg)
        setformSubmissionStart(false)
      }
    }
  }

  const onSubmitHandler = (event) => {
    event.preventDefault();
    setformSubmissionStart(true)
    DigitalHelp(onSubmitCallBack, props.formData);
  }

  const onInputChange = (event) => {
    const inputFieldName = event.target.name;
    const targetValue = event.target.value;
    const targetType = event.target.type;
    switch (targetType) {
      case 'text':
        if (inputFieldName === 'name') {
          props.formData.delete('name');
          props.formData.append('name', targetValue);
        }
        else
          if (inputFieldName === 'contact_number') {
            props.formData.delete('contact_number');
            props.formData.append('contact_number', targetValue);
          }
          else
            if (inputFieldName === 'email') {
              props.formData.delete('email');
              props.formData.append('email', targetValue);
            }
            else
              if (inputFieldName === 'country') {
                props.formData.delete('country');
                props.formData.append('country', targetValue);
              }
              else
                if (inputFieldName === 'imagelink') {
                  props.formData.delete('imagelink');
                  props.formData.append('imagelink', targetValue);
                }
        break;
      case 'textarea':
        props.formData.delete('message');
        props.formData.append('message', targetValue);
        break;
      default:
        break;
    }
    props.formData = props.formData
  }

  const closeModal = () => {
    setshowModal(false)
  }

  const queryReport = () => {
    getQueryReport()
  }

  const meta = {
    title: 'Ethereal Machines - CAD/CAM Design or Programming Help',
    meta: {
      property: {
        'title': 'Ethereal Machines - CAD/CAM Design or Programming Help',
        'description': 'Ethereal Machines is glad to assist you with CAD/CAM programming for CNC machining and 3D Printing.',
      }
    }
  };
  return (
    <React.Fragment>
      <Header />
      <Modal show={showModal} clicked={closeModal}>
        <FormSuccessMsg />
      </Modal>
      <div className="page page--contact-us">

        {/* <Helmet>
        <title>Design Help | Ethereal Machines-Bangalore, India.</title>
        <meta name="description" content="Ethereal Machines – get in touch with us."/>
        </Helmet> */}

        <DocumentMeta {...meta} />
        <PageBanner heading={"NEED HELP WITH CAD/CAM PROGRAMMING?"} subHeading={" "} classValue={"page-banner--design-help"} />
        <section className="section section--design-help-form">
          <div className="container">
            {
              props.session.session
                ?
                <div>
                  <button className="form-btn" onClick={queryReport}>Query Report</button>
                </div>

                :
                null
            }
          </div>
          <SectionHeading name={"Details form"} classValue={"u-margin-bottom-big u-text-center"} hasSubHeading subHeading={"We'd Love to hear about your Project and Help you get started"} />
          <DesignHelpForm onInputChange={onInputChange} onSubmitHandler={onSubmitHandler} formSubmissionStart={formSubmissionStart} errorMsg={errorMsg} natureOfQuery={props.natureOfQuery} isStateRequired={props.isStateRequired} showLoader={formSubmissionStart} />

        </section>

      </div>
      <Footer />
    </React.Fragment>
  );

}

const mapStateToProps = (state) => {
  return {
    session: state.login
  }
}

export default connect(mapStateToProps)(DesignHelp);